import React, { Component } from 'react';
import { connect } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom'
import { url } from '../redux/api/endpoints';
import { getObjectbyId, toggleArray } from '../components/utils'

// actions
import JobsActions from '../redux/redux-sauce/jobs';
import IsubsActions from '../redux/redux-sauce/isubs';


function JobBlock (props) {
  const { job } = props
  return (
    <div className='card card-style my-2'>
      <div class="content text-left">
          <h4 className='font-16 font-500 float-left'>{job.title}</h4>
          <h4 className='font-18 float-right'>{job.budget}</h4>
          <div className="clearfix"></div>
          <p className='text-left float-left mb-0 font-12 font-500'>{job.zakazchik}</p>
          <p className='text-left float-right mb-0 font-11 font-500'>{job.category}</p>
          <div className="clearfix"></div>
          <p className="text-left mb-0">
            {job.description}
          </p>
          <p className='text-left float-left mb-0'>{job.address}</p>
          {job.start_date ? 
              <p className='text-left float-right mb-0'>c {job.start_date}  {job.end_date}</p>
              :
              job.end_date ?
                <p className='text-left float-right mb-0'> {job.end_date}</p>
                :
                <p className='text-left float-right mb-0'>  </p>
            }
          <div className="clearfix"></div>
          <p className='text-left font-10'>{job.created_ago}</p>
      </div>
    </div>
  )
}


function Categories (props){
  const { categories } = props
  
  return (
    <div>
      {categories.length > 0 && categories.map(category => 
          <button 
            className="btn btn-xs mb-1 ml-1 rounded-xl text-uppercase font-900 shadow-s bg-blue2-light
              text-wrap float-left" 
            // className={props.filterCategories.includes(category.id) ? 'btn-category btn-category-active' : 'btn-category' }
            key={category.id} onClick={props.clickCategory} 
            data-id={category.id}
            >
              {category.ru_name}
          </button>
        )}
      <div className="clearfix"></div>
    </div>
  )
}

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      needToRefresh: false,
    }
    this.clickCategory = this.clickCategory.bind(this);
    this.clickSubscribe = this.clickSubscribe.bind(this);
    this.clickUnsubscribe = this.clickUnsubscribe.bind(this);
    this.clickSubscribeAll = this.clickSubscribeAll.bind(this);
  }

  componentDidMount() {
    this.props.getJobs()
  }

  clickCategory(e) {
    const { id } = e.target.dataset
    this.props.toggleFilterCategories(id)
    this.setState({...this.state, needToRefresh: true})
  }

  clickSubscribe(){
    this.props.createOrChangeIsub({categories: this.props.filterCategories})
  }

  clickSubscribeAll(){
    let categories = []
    this.props.state.jobs.categories.map(cat => categories = toggleArray(categories, cat.id))
    this.props.createOrChangeIsub({categories: categories})
  }

  clickUnsubscribe(){
    this.props.createOrChangeIsub({})
  }

  refreshList () {
    if (!this.props.state.jobs.listFetching && this.state.needToRefresh){
      setTimeout(() => {
        this.setState({...this.state, needToRefresh: false})
        const { filterCategories } = this.props
        this.props.getJobs({category: filterCategories})
      }, 10)
    }
  }

  render() {
    this.refreshList()
    const { jobs, categories, listFetching, errorList } = this.props.state.jobs
    const { filterCategories, isub } = this.props.state.isubs
    const { user } = this.props.state.auth
    // console.log(this.props.state)
    return (
        <div>
          <div className=''>
            <div className="page-title page-title-large">
              <h2 data-username={user ? user.nickname : ''} className="greeting-text">
                
              </h2>
              <a href="#" data-menu="menu-main" className="bg-fade-gray1-dark shadow-xl preload-img" 
                data-src="images/avatars/5s.png"></a>
            </div>
            <div className="card header-card shape-rounded" data-card-height="210">
              <div className="card-overlay bg-highlight opacity-95"></div>
              <div className="card-overlay dark-mode-tint"></div>
              <div className="card-bg preload-img" data-src="images/pictures/20s.jpg"></div>
            </div>
            <div className="card card-style mb-3">
              <div className="content">
                <Categories 
                  categories={categories} 
                  getJobs={this.props.getJobs} 
                  filterCategories={filterCategories}
                  toggleFilterCategories={this.props.toggleFilterCategories}
                  clickCategory={this.clickCategory}
                />
                {categories && isub && isub.categories.length > 0 &&
                  <div>
                    <p className='my-2 mr-1 ml-3 font-11 text-wrap font-italic' style={{"line-height": "95%"}}>
                         {isub.categories.map((cat, idx) =>
                        cat.ru_name + ', '
                      )}
                    </p>
                    <button className='btn btn-m mb-1 ml-1 rounded-xl text-uppercase font-900
                       shadow-s bg-red2-light float-right' onClick={this.clickUnsubscribe}>
                         
                    </button>
                  </div>
                }
                {filterCategories.length > 0 
                  ? <button className='btn btn-m mb-1 ml-1 rounded-xl text-uppercase font-900
                     shadow-s bg-blue1-dark float-left' onClick={this.clickSubscribe}>
                       ({filterCategories.length})
                    </button>
                  : isub && isub.categories < 1 && 
                    <button className='btn btn-m mb-1 ml-1 rounded-xl text-uppercase font-900
                     shadow-s bg-blue1-dark float-left' onClick={this.clickSubscribeAll}>
                         
                    </button>
                }
              </div>
            </div>
            {listFetching && <p>Loading...</p>}
            {jobs.length > 0 && jobs.map(job =>
              <JobBlock job={job} key={job.id}/>
              )}
          </div>
        </div>
    );
  }
}

const mapStateToProps = (state) => ({
  notifications: state.notifications,
  state: state,
  filterCategories: state.isubs.filterCategories
})

const mapDispatchToProps = (dispatch) => ({
  getCategories: query => dispatch(JobsActions.getCategoriesRequest(query)),
  getJobs: query => dispatch(JobsActions.getJobsRequest(query)),

  toggleFilterCategories: catId => dispatch(IsubsActions.toggleFilterCategories(catId)),
  createOrChangeIsub: catId => dispatch(IsubsActions.createOrChangeIsubRequest(catId)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Main);
